import newNativeProject from "./newNativeProject";
import newWebProject from "./newWebProject";
import { prompt } from "../../utils";

export default async (name, command) => {
    let target = await prompt('What Platform would you like to Develop for?', [
        'Native + Web',
        'Web Only'
    ])
    switch(target){
        case 'Native + Web': return newNativeProject(name, command)
        case 'Web Only': return newWebProject(name, command)
    }
}